import Vue from 'vue'
import Vuex from 'vuex'
// huwenbo
// import ReservationRecordTab from './modules/ReservationRecordTab'
import NurserySetting from './modules/NurserySetting'
import Login from './modules/Login' // 
import UnitManagement from './modules/UnitManagement' // 

// import AddKPtemplate from './modules/AddKPtemplate' // 
// import Coupons from './modules/Coupons' // 


Vue.use(Vuex)
const state = {
  loading: false
}
// loading
const mutations = {
  SETLOADING(state, payload) {
    // 
    state.loading = payload
  }
}
const store = new Vuex.Store({
  modules: {
    // huwenbo
    // ReservationRecordTab
    NurserySetting,
    Login,
    UnitManagement
    // AddKPtemplate
    // Coupons

  },
  state,
  mutations
})

export default store



