<template>
  <div>
    <!--  -->
    <el-form ref="form" :model="form" label-width="150px">
      <el-form-item label="">
        <el-input v-model="form.name" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input type="textarea" :rows="2" placeholder="" v-model="form.textarea"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-upload
          action="https://jsonplaceholder.typicode.com/posts/"
          list-type="picture-card"
          :on-preview="handlePictureCardPreview"
          :on-remove="handleRemove"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt />
        </el-dialog>

        <p>jpgpng100KB~1MB3705306</p>
      </el-form-item>

      <el-form-item label="">
        <el-upload
          action="https://jsonplaceholder.typicode.com/posts/"
          list-type="picture-card"
          v-bind:data="{FoldPath:'',SecretKey:''}"
          v-bind:on-progress="uploadVideoProcess"
          v-bind:on-success="handleVideoSuccess"
          v-bind:before-upload="beforeUploadVideo"
          v-bind:show-file-list="false"
        >
          <video
            v-if="videoForm.showVideoPath !='' && !videoFlag"
            v-bind:src="videoForm.showVideoPath"
            class="avatar video-avatar"
            controls="controls"
          ></video>
          <i
            v-else-if="videoForm.showVideoPath =='' && !videoFlag"
            class="el-icon-plus avatar-uploader-icon"
          ></i>
          <el-progress
            v-if="videoFlag == true"
            type="circle"
            v-bind:percentage="videoUploadPercent"
            style="margin-top:7px;"
          ></el-progress>
        </el-upload>

        <p>jpgpng100KB~1MB640480mp410~40MB640480</p>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button @click="handleNewly"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import mixin from "../Mixins";

export default {
  mixins: [mixin],
  data() {
    return {
      form: {
        name: "",
        textarea: ""
      },
      dialogImageUrl: "",
      dialogVisible: false,

      videoFlag: false,
      //
      videoUploadPercent: "",
      //
      isShowUploadVideo: false,
      //
      videoForm: {
        showVideoPath: ""
      }
    };
  },
  methods: {
    onSubmit() {
      this.SETOPTIONS({ indexCustomInfoManagement: 0 });
    },

    handleNewly() {
      this.SETOPTIONS({ indexCustomInfoManagement: 0 });
    },

    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },

    //
    beforeUploadVideo(file) {
      var fileSize = file.size / 1024 / 1024 < 50;
      if (
        [
          "video/mp4",
          "video/ogg",
          "video/flv",
          "video/avi",
          "video/wmv",
          "video/rmvb",
          "video/mov"
        ].indexOf(file.type) == -1
      ) {
        layer.msg("");
        return false;
      }
      if (!fileSize) {
        layer.msg("50MB");
        return false;
      }
      this.isShowUploadVideo = false;
    },
    //
    uploadVideoProcess(event, file, fileList) {
      this.videoFlag = true;
      this.videoUploadPercent = file.percentage.toFixed(0) * 1;
    },
    //
    handleVideoSuccess(res, file) {
      this.isShowUploadVideo = true;
      this.videoFlag = false;
      this.videoUploadPercent = 0;

      //
      //if (file.status == 'success' ) {
      //    this.videoForm.showVideoPath = file.url;
      //} else {
      //     layer.msg("");
      //}

      //
      if (res.Code == 0) {
        this.videoForm.showVideoPath = res.Data;
      } else {
        layer.msg(res.Message);
      }
    }
  }
};
</script>

<style lang="stylus"></style>


