<template>
  <div>
    <el-dialog title="" :visible.sync="dialogVisible" width="30%" :show-close="false">
      <p></p>
      <p></p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false
    };
  },
  methods: {}
};
</script>

<style lang="stylus"></style>


