<template>
  <div>
    <div class="template-form-administration">
      <el-form ref="formInline" :model="formInline" label-width="120px">
        <el-row :gutter="20">
          <el-col :span="8">
            <el-form-item label="">
              <el-select v-model="formInline.region" placeholder="">
                <el-option label="" value="all"></el-option>
                <el-option label="" value="beijing1"></el-option>
                <el-option label="" value="beijing2"></el-option>
              </el-select>
            </el-form-item>
          </el-col>

          <el-col :span="8">
            <el-form-item label="">
              <el-date-picker
                v-model="formInline.value2"
                align="right"
                type="date"
                placeholder=""
                :picker-options="pickerOptions"
              ></el-date-picker>
            </el-form-item>
          </el-col>

          <el-col :span="8">
            <el-form-item label="">
              <el-select v-model="formInline.region" placeholder="">
                <el-option label="" value="all"></el-option>
                <el-option label="" value="beijing1"></el-option>
                <el-option label="-" value="beijing2"></el-option>
                <el-option label="-" value="beijing3"></el-option>
                <el-option label="-" value="beijing4"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="20">
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="formInline.user" placeholder=""></el-input>
            </el-form-item>
          </el-col>

          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="formInline.user" placeholder=""></el-input>
            </el-form-item>
          </el-col>

          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="formInline.user" placeholder=""></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
  </div>
</template>

<script>
import mixin from "./Mixins";

export default {
  mixins: [mixin],

  data() {
    return {
      formInline: {
        value2: ""
      },

      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      }
    };
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    }
  }
};
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">
.template-form-administration {
}
</style>






