<template>
  <div>
    <div>
      <!--  -->
      <p></p>xxx
      <p></p>xxx
      <el-row>
        <span>XXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX</span>
        <el-button type="danger" @click="dialogVisible = true"></el-button>
      </el-row>

      <el-row>
        <span>XXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX</span>
        <el-button type="danger" @click="dialogVisible = true"></el-button>
      </el-row>

      <el-row>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button></el-button>
      </el-row>
    </div>

    <el-dialog title="" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
      <p></p>
      <p></p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        region: ""
      },
      dialogVisible: false
    };
  },
  methods: {
    handleClose(done) {
      this.$confirm("")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    onSubmit() {
      console.log("submit!");
    }
  }
};
</script>

<style lang="stylus" scoped>
.TemplateSettingsText-list {
  .grid-content {
    text-align: center;
    width: 300px;
    height: 300px;

    img {
      width: 100%;
      height: 100%;
    }

    p {
      line-height: 50px;
    }
  }
}
</style>




