<template>
  <div>
    <!--  -->
    <p>XXXXXXXXXXXX</p>
    <p>XXXXXXX</p>

    <el-radio-group v-model="radio" class="TemplateSettingsMusic">
      <el-radio :label="i" v-for="(item,i) in list" :key="i">
        <div class="content">
          <img src="@/assets/images/imgcode.png" alt="imgcode" />
          <p>{{item}}</p>
        </div>
      </el-radio>
    </el-radio-group>
  </div>
</template>

<script>
export default {
  data() {
    return {
      radio: "",
      list: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ]
    };
  }
};
</script>

<style lang="stylus">
.TemplateSettingsMusic {
  .el-radio {
    text-align: center;
  }

  .el-radio__label {
    font-size: 14px;
    padding-left: 10px;
    display: inline-block;
  }

  .el-radio__input {
    left: 10px;
  }
}
</style>

<style lang="stylus" scoped>
.TemplateSettingsMusic {
  .content {
    img {
      width: 124px;
      height: 124px;
    }
  }
}
</style>




