<template>
  <div>
    <el-table :data="tableData" border style="width: 100%">
      <el-table-column prop="date" label="" width="180"></el-table-column>
      <el-table-column prop="name" label="" width="180"></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="address" label="">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
          <el-button type="text" size="small"></el-button>
          <el-button type="text" size="small"></el-button>
          <el-button type="text" size="small"></el-button>
          <el-button type="text" size="small"></el-button>
          <el-button type="text" size="small"></el-button>
          <el-button type="text" size="small"></el-button>
          <el-button type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage4"
      :page-sizes="[10, 20, 30, 40]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="40"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [
        {
          date: "2016-05-02",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-04",
          name: "",
          address: " 1517 "
        },
        {
          date: "2016-05-01",
          name: "",
          address: " 1519 "
        },
        {
          date: "2016-05-03",
          name: "",
          address: " 1516 "
        }
      ],
      currentPage4: 1
    };
  },
  methods: {
    handleSizeChange(val) {
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
    }
  }
};
</script>

<style lang="stylus" scoped rel="stylesheet/stylus">
.template-form-administration {
}
</style>






