<template>
  <div>
    <!--  -  -->
    <el-form ref="form" :model="form" label-width="150px">
      <el-form-item label="">
        <el-radio-group v-model="form.radio">
          <el-radio :label="3"></el-radio>
          <el-radio :label="6"></el-radio>
          <el-radio :label="9"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="form.name" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <span>123456</span>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="form.name" placeholder=""></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button @click="handleNewly"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import mixin from "../Mixins";

export default {
  mixins: [mixin],
  data() {
    return {
      form: {
        name: "",
        region: "",
        data: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        value1: "",
        radio: ""
      }
    };
  },
  methods: {
    onSubmit() {
      this.SETOPTIONS({ indexAccountManagement: 0 });
    },

    handleNewly() {
      this.SETOPTIONS({ indexAccountManagement: 0 });
    }
  }
};
</script>

<style lang="stylus"></style>


