<template>
  <div>
    <!--  tab-->
    <el-row>
      <el-button type="primary" @click="handleNewly(1)"></el-button>
    </el-row>

    <el-table :data="tableData" border style="width: 100%">
      <el-table-column prop="date" label="" width="180"></el-table-column>
      <el-table-column prop="name" label="" width="180"></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="address" label="">
        <template slot-scope="scope">
          <el-button @click="editClick(scope.row)" type="text" size="small"></el-button>
          <el-button @click="stopClick(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage4"
      :page-sizes="[10, 20, 30, 40]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="40"
    ></el-pagination>

    <el-dialog title="" :visible.sync="dialogVisible" width="30%" :show-close="false">
      <p></p>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
        <el-button @click="dialogVisible = false"> </el-button>
      </span>
    </el-dialog>

    <el-dialog title="" :visible.sync="dialogVisible2" width="30%" :show-close="false">
      <p></p>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible2 = false"> </el-button>
        <el-button @click="handleNewly(1)"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import mixin from "../Mixins";

export default {
  mixins: [mixin],
  data() {
    return {
      tableData: [
        {
          date: "2016-05-02",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-04",
          name: "",
          address: " 1517 "
        },
        {
          date: "2016-05-01",
          name: "",
          address: " 1519 "
        },
        {
          date: "2016-05-03",
          name: "",
          address: " 1516 "
        }
      ],
      currentPage4: 1,
      dialogVisible: false,
      dialogVisible2: false
    };
  },
  methods: {
    handleSizeChange(val) {
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
    },
    editClick() {
      this.dialogVisible = true;
    },
    stopClick() {
      this.dialogVisible2 = true;
    },

    handleNewly(index) {
      if (typeof index === "number")
        this.SETOPTIONS({ indexAccountManagement: index });
      return false;
    }
  }
};
</script>

<style lang="stylus" scoped rel="stylesheet/stylus"></style>






