<template>
  <div>
    <!--  -tab -->
    <el-row>
      <el-button type="primary" @click="handleNewly(1)"></el-button>
    </el-row>

    <el-row>
      <span>100</span>
    </el-row>

    <el-table :data="tableData" border style="width: 100%">
      <el-table-column prop="date" label="" width="180"></el-table-column>
      <el-table-column prop="name" label="" width="180"></el-table-column>
      <el-table-column prop="num" label=""></el-table-column>
      <el-table-column prop="num" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button @click="modifyClick(scope.row)" type="text" size="small"></el-button>
          <el-button @click="deleteClick(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage4"
      :page-sizes="[10, 20, 30, 40]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="40"
    ></el-pagination>

    <el-dialog title="" :visible.sync="dialogVisible" width="30%" :show-close="false">
      <p></p>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
        <el-button @click="dialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import mixin from "../Mixins";

export default {
  mixins: [mixin],
  data() {
    return {
      tableData: [
        {
          date: "YYYYMMDD",
          name: "XXXXXXX",
          num: 0
        },
        {
          date: "YYYYMMDD",
          name: "222",
          num: 0
        },
        {
          date: "YYYYMMDD",
          name: "XXXXXXX",
          num: 0
        },
        {
          date: "YYYYMMDD",
          name: "qqqqqqqqqqq",
          num: 0
        }
      ],
      currentPage4: 1,
      dialogVisible: false
    };
  },
  methods: {
    handleSizeChange(val) {
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
    },
    modifyClick() {
      console.log("");
    },
    deleteClick() {
      this.dialogVisible = true;
    },

    handleNewly(index) {
      if (typeof index === "number")
        this.SETOPTIONS({ indexHallManagement: index });
      return false;
    }
  }
};
</script>

<style lang="stylus" scoped rel="stylesheet/stylus"></style>






